.main-container__image {
  position: relative;
  z-index: 1;
  overflow: hidden;
  height: 389px;
  background-image: url("../img/photo-main-sedona.jpg");
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  padding-top: 90px;
  background-size: cover;
}

.main-container__image-caption {
  width: 100%;
  height: 214px;
}

.main-container__image-triangle {
  position: absolute;
  width: 1200px;
  height: 57px;
  bottom: 0;
}

@media (min-width: 768px) {
  .main-container__image {
    padding-top: 76px;
    height: 518px;
  }

  .main-container__image-caption {
    height: 350px;
  }
}
